.slider-container {
  --progress-bar-height: 8px;
}

.slider-container {
  /* margin-bottom:-10px; */
  position: relative;
  width: 60%;
  margin:auto;
}

/* Background */
.slider-container::before {
  content: '';
  background-color: black;
  width: 99%;
  height: calc(var(--progress-bar-height) - 1px);
  display: block;
  position: absolute;
  border-radius: 10px;
  top: 18%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  margin-bottom: 10px;
}

/* Custom Progress Bar */
.progress-bar-cover {
  background-color:rgba(255, 0, 0, 0.678);
  width: 0%;
  height: var(--progress-bar-height);
  display: block;
  position: absolute;
  border-radius: 10px;
  top: 18%;
  transform: translateY(-50%);
  z-index: 1;
  user-select: none;
  pointer-events: none;
  /* margin-left:60%; */
}

/*  Hide Original */
.range {
  -webkit-appearance: none;
  background-color: rgba(240, 9, 9, 0.397);
  height: 30px;
  width: 100%;
  cursor: pointer;
  opacity: 0;
  margin: 0 auto;
}
